.playlist-card{
    display: flex;
    margin:3px;
    padding:5px;
    width:60vw;
    border-radius: 10px;
    justify-content: space-between;
}
.playlist-card:hover{
    background-color: #526D82;
    transition: all 0.3s ease-in-out;
}
h4{
    list-style-type: none;
    color:white;
}